import * as sakuraba from "sakuraba";

/**  */
export function flipSide(side: PlayerSide): PlayerSide{
  if(side === 'p1') return 'p2';
  if(side === 'p2') return 'p1';
  return side;
}

/**  */
export function getMegamiDispName(megami: sakuraba.Megami): string{
    let data = sakuraba.MEGAMI_DATA[megami];
    return `${data.name}(${data.symbol})`
}

/**  */
export function logIsVisible(log: state.LogRecord, side: SheetSide): boolean{
    if(log.visibility === 'shown') return true;
    if(log.visibility === 'ownerOnly' && log.playerSide === side) return true;
    if(log.visibility === 'outerOnly' && log.playerSide !== side) return true;
    return false;
}

/**  */
export function judgeCardOpenState(
      card: state.Card
    , handOpenFlag: boolean
    , cardSide?: PlayerSide
    , cardRegion?: CardRegion
): CardOpenState{
    if(cardSide === undefined) cardSide = card.side;
    if(cardRegion === undefined) cardRegion = card.region;
    let cardData = sakuraba.CARD_DATA[card.cardId];

    if(cardRegion === 'used' || cardRegion === 'on-card' || cardRegion === 'extra' || (cardData.baseType === 'special' && card.specialUsed)){
        // ON
        return 'opened';
    } else if(cardRegion === 'hand'){
        // 
        // ON
        return (handOpenFlag ? 'opened' : 'ownerOnly');
    }
    // 
    return 'hidden';
}

/** HTML */
export function getDescriptionHtml(cardId: string): string{
  let cardData = sakuraba.CARD_DATA[cardId];
  let cardTitleHtml = `<ruby><rb>${cardData.name}</rb><rp>(</rp><rt>${cardData.ruby}</rt><rp>)</rp></ruby>`
  let html = `<div class='ui header' style='margin-right: 2em;'>${cardTitleHtml}`

  html += `</div><div class='ui content'>`
  if(cardData.baseType === 'special'){
    html += `<div class='ui top right attached label'>: ${cardData.cost}</div>`;
  }

  let typeCaptions = [];
  if(cardData.types.indexOf('attack') >= 0) typeCaptions.push("<span class='card-type-attack'></span>");
  if(cardData.types.indexOf('action') >= 0) typeCaptions.push("<span class='card-type-action'></span>");
  if(cardData.types.indexOf('enhance') >= 0) typeCaptions.push("<span class='card-type-enhance'></span>");
  if(cardData.types.indexOf('variable') >= 0) typeCaptions.push("<span class='card-type-variable'></span>");
  if(cardData.types.indexOf('reaction') >= 0) typeCaptions.push("<span class='card-type-reaction'></span>");
  if(cardData.types.indexOf('fullpower') >= 0) typeCaptions.push("<span class='card-type-fullpower'></span>");
  html += `${typeCaptions.join('/')}`;
  if(cardData.range !== undefined){
      if(cardData.rangeOpened !== undefined){
        html += `<span style='margin-left: 1em;'> []${cardData.range} []${cardData.rangeOpened}</span>`
      } else {
        html += `<span style='margin-left: 1em;'>${cardData.range}</span>`;
      }
  }
  html += `<br>`;
  if(cardData.types.indexOf('enhance') >= 0){
      html += `: ${cardData.capacity}<br>`;
  }

  if(cardData.damageOpened !== undefined){
    // 
    html += `[] ${cardData.damage}<br>`;
    html += `${cardData.text.replace(/----\n/g, '<hr>').replace(/\n/g, '<br>')}`;
    html += (cardData.text ? '<br>' : '');
    html += `[] ${cardData.damageOpened}<br>`;
    html += `${cardData.textOpened.replace(/----\n/g, '<hr>').replace(/\n/g, '<br>')}`;
  } else if(cardData.textOpened) {
    // 
    html += `[] ${cardData.text.replace(/----\n/g, '<hr>').replace(/\n/g, '<br>')}`;
    html += (cardData.text ? '<br>' : '');
    html += `[] ${cardData.textOpened.replace(/----\n/g, '<hr>').replace(/\n/g, '<br>')}`;

  } else {
    if(cardData.damage !== undefined){
        html += `${cardData.damage}<br>`;
    }
    html += `${cardData.text.replace(/----\n/g, '<hr>').replace(/\n/g, '<br>')}`;
  }
  html += `</div>`;

  if(cardData.megami === 'kururu'){
    html = html.replace(/<([]+)>/g, (str, arg) => {
        let replaced = arg.replace(/+/, (str2) => `<span class='card-type-attack'>${str2}</span>`)
                          .replace(/+/, (str2) => `<span class='card-type-action'>${str2}</span>`)
                          .replace(/+/, (str2) => `<span class='card-type-enhance'>${str2}</span>`)
                          .replace(/+/, (str2) => `<span class='card-type-reaction'>${str2}</span>`)
                          .replace(/+/, (str2) => `<span class='card-type-fullpower'>${str2}</span>`)
        return `<${replaced}>`;
    });
  }

  return html;
}

/**  */
export function getCardRegionTitle(selfSide: PlayerSide, side: PlayerSide, region: CardRegion){
    let titleBase = ``;
    if(region === 'hand'){
        titleBase = "";
    }
    if(region === 'hidden-used'){
        titleBase = "";
    }
    if(region === 'library'){
        titleBase = "";
    }
    if(region === 'special'){
        titleBase = "";
    }
    if(region === 'used'){
        titleBase = "";
    }

    // 
    if(selfSide !== side){
        return `${titleBase}`
    } else {
        return titleBase;
    }
}

/**  */
export function getSakuraTokenRegionTitle(selfSide: PlayerSide, side: PlayerSide, region: SakuraTokenRegion, linkedCard?: state.Card){
    let titleBase = ``;
    if(region === 'aura'){
        titleBase = "";
    }
    if(region === 'life'){
        titleBase = "";
    }
    if(region === 'flair'){
        titleBase = "";
    }
    if(region === 'distance'){
        titleBase = "";
    }
    if(region === 'dust'){
        titleBase = "";
    }
    if(region === 'on-card'){
        let cardData = sakuraba.CARD_DATA[linkedCard.cardId];
        titleBase = `[${cardData.name}]`;
    }

    // 
    if(selfSide !== side && side !== null){
        return `${titleBase}`
    } else {
        return titleBase;
    }
}